<template>
    <div class="vue-scroller">
        <div class="wrapper">
            <scroller class="scroller-commponents" height='400' :on-refresh="refresh" :on-infinite="infinite" ref="myscroller">
                <div class="order-box" v-for="(item, index) in orderList" :key="index">
                    
                </div>
            </scroller>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                orderList:[],
                page: 0,
                all_page: 1,
            }
        },
        methods: {
            // 下拉刷新
            refresh (done) {
                setTimeout(()=>{
                    this.goodsAll();
                    done();
                },1500)
            },
            //上拉加载更多
            infinite (done) {
                console.log('infinite');
                setTimeout(()=>{
                    if(this.page >= this.all_page){
                        this.$refs.myscroller.finishInfinite(true);
                    }else{
                        this.page++;
                        this.getorderList(done);
                    }
                }, 500)
            },
            // 如果涉及到tab栏切换，需要重新设置scroller的状态.finishInfinite(false)参数为false时会从新调用一次上拉函数，从而重置当前scroller的状态
            goodsAll(){
                this.page = 0
                this.all_page = 1
                this.$refs.myscroller.finishInfinite(false);
                this.orderList = [];
            },
            getorderList(done){
                console.log('++');
                var list = []
                var curr_page = 1
                var all_page = 1

                this.orderList.push.apply(this.orderList, list) // 合并
                this.$refs.myscroller.finishInfinite(true) // 加载完成

                this.page = curr_page // 当前页码数
                this.all_page = all_page // 总页码数
                done();
            }
        }
    }

    // triggerPullToRefresh() 触发下拉刷新

    // finishPullToRefresh() 完成下拉刷新

    // this.$refs.my_scroller.finishInfinite(false)

    // finishInfinite(isNoMoreData :Boolean) 当参数为false时，上拉获取数据可以重新调用。当参数为true，上拉获取数据回调函数停止使用,下拉下部不再显示loading，会显示‘暂无更多数据’

</script>

